// components
import Nav from "Shared/ts/components/nav";
import Carousel from "Shared/ts/components/carousel";
import ToolTip from "Shared/ts/components/tooltip";
import Accordion from "Shared/ts/components/accordion"; // adapters

import SlideCarouselAdapter from "Shared/ts/api/carousel/slide/adapters/slide-carousel"; // observers

import { observer } from "Shared/ts/observers/intersection"; // utils

import LoadItem from "Shared/ts/utils/load-item";
import ValidateCommon from "Shared/ts/api/validate/validate-common"; // applications

import { initializeValidateEventWithPromoCode } from "Shared/ts/applications/form";
var nav = new Nav("nav");
observer("[data-nav-controller]", {
  inRange: function inRange() {
    var _nav$root;

    (_nav$root = nav.root) == null ? void 0 : _nav$root.classList.remove("nav--is-fixed");
  },
  outRange: function outRange() {
    var _nav$root2;

    (_nav$root2 = nav.root) == null ? void 0 : _nav$root2.classList.add("nav--is-fixed");
  },
  unObserve: false
});
var viewHeaderNav = document.querySelector(".view--header-nav");
observer("[data-viewport]", {
  inRange: function inRange() {
    viewHeaderNav == null ? void 0 : viewHeaderNav.classList.add("view--is-hidden");
  },
  outRange: function outRange() {
    viewHeaderNav == null ? void 0 : viewHeaderNav.classList.remove("view--is-hidden");
  },
  unObserve: false,
  options: {
    threshold: 0.25
  }
});
observer(".slide--carousel", {
  inRange: function inRange(element) {
    var carousel = new Carousel(new SlideCarouselAdapter(element));
    carousel.enablePrevNextControls();
  }
});
observer("[data-src-iframe]", {
  inRange: function inRange(record) {
    return new LoadItem(record, {
      tag: "iframe",
      src: "data-src-iframe"
    });
  }
});
addEventListener("load", function () {
  new ToolTip(document.querySelectorAll(".link"), "link--is-active");
});
observer('[data-validate-form="promocode"]', {
  inRange: function inRange(form) {
    var submit = form.querySelector('[type="button"]');
    if (!submit) return;
    initializeValidateEventWithPromoCode(form, submit);
  }
});
observer("form", {
  inRange: function inRange(form) {
    var validateCommon = new ValidateCommon({
      form: form,
      attribute: "data-required",
      inputEvents: ["keyup", "blur", "change"],
      comboboxEvents: ["change", "blur"]
    });
    validateCommon.validateOnSubmit();
    validateCommon.processInputEvents();
    validateCommon.processComboboxEvents();
  }
});
observer(".accordion", {
  inRange: function inRange(element) {
    return new Accordion(element);
  }
});